import './App.css';
import Button from "@material-ui/core/Button";
import { TextField } from '@material-ui/core';
import React, { useState } from 'react';


function App() {

  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('If you have a cookie hopefully this will change');

  const handleClick = () => {
    setOutputText(inputText);
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>Welcome to the K6 test app!</h1>
        <p></p>
        <Button className="button" variant="contained" color='primary'>Make me a cookie!</Button>
        <p></p>
        <TextField
                  onChange={(event) => {setInputText(event.target.value)}}
                  id="outlined" 
                  label="Write something!" 
                  variant="outlined"
                  style={{
                    backgroundColor: "white",
                    width:500,
                }}/>
        <p></p>
        <Button className="button" variant="contained" color='primary' onClick={handleClick}>Submit text</Button>
        <p></p>
              <h3>{outputText}</h3>
      </header>
    </div>
  );
}

export default App;
